
.black-nav {
  display: flex;
  background-color: #333;
  width: 100%;
  color: #fff;
  padding-left: 20px;
 
}

div {
  box-sizing: border-box;
  position: relative;
}

.list {
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid gray;
}

button {
  margin: 1%;
}

.modal {
  position: absolute;
  width: 70%;
  height: 70%;
  top: 58%;
  left: 10%;
  transform: translate(-50% -50%);
  padding: 30px;
  background-color: #eee;
  text-align: left;
}

h4:hover {
  cursor: pointer;
}

